.rotate180 {
  transform: rotate(180deg);
}
.rotate90 {
  transform: rotate(90deg);
}
.dnd-container {
  border: 0;
  width: 100%;
  height: 100%;
}
.browser-mockup {
  display: flex;
  flex: 1;
  position: relative;
  border-radius: 3px 3px 0 0;
  -webkit-box-shadow: 0 0.1em 0.5em 0 rgba(0, 0, 0, 0.28);
  box-shadow: 0 0.1em 0.5em 0 rgba(0, 0, 0, 0.28);
}
.browser-mockup :global .ant-spin-container {
  height: 100%;
}
.header-title:hover {
  color: #fa541c;
}
.guides {
  color: #b0b0b0;
}
.guides:hover {
  color: #fff;
}
.cart {
  background-color: #424242;
  color: #b0b0b0;
  font-size: 12px;
}
.cart:hover {
  background-color: #fff;
  color: #fa541c;
}
.search-Icon {
  border-color: #e0e0e0;
  border-style: solid;
  border-width: 1px 1px 1px 0;
  color: #616161;
}
.search-Icon:hover {
  background-color: #fa541c;
  color: #fff;
  border-color: #fa541c;
}
.btn:hover {
  color: #fa541c;
}
.wrapper {
  background: #f7f7f7;
  display: flex;
  flex-direction: column;
  box-shadow: 2px 0 4px 0 rgba(174, 174, 174, 0.5);
}
.content {
  display: flex;
  flex: 1 1;
  flex-direction: row;
}
.left-preview {
  height: 100vh;
  overflow-x: hidden;
  background: #00000000;
}
.props-shadow {
  overflow: hidden;
  background: #ffffff;
  height: 100%;
  box-shadow: 0 2px 4px 0 rgba(174, 174, 174, 0.5);
}
.canvas-container {
  display: flex;
  flex: 1 1;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  background-color: #f5f5f5;
  height: calc(calc(100vh - 120px) + 50px);
  overflow: hidden;
}
.brick-tree {
  height: calc(100vh - 100px);
}
.preview-container {
  height: calc(calc(100vh - 120px) - 10px);
}
.tabs-container {
  background: #fff;
  width: 100%;
  height: calc(100vh - 200px);
}
.input {
  width: 100px;
  height: 25px;
  background-color: red;
}
.placeholder-container {
  display: flex;
  flex: 1 1;
  height: 400px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.placeholder-container img {
  width: 80px;
  height: 80px;
}
.demo-container {
  width: 500px;
  height: 500px;
  background-color: red;
}
.demo-container .demo-container0 {
  width: 300px;
  height: 300px;
  background-color: green;
}
.demo-container .demo-container1 {
  width: 300px;
  height: 300px;
  background-color: saddlebrown;
}
